.programms__tab-link {
    text-decoration: none;
    color: #004084;
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 8px;    
    margin-left: 60px;
}

.programms__tab-link:last-of-type {
    margin-top: 26px;
    margin-bottom: 0px;
    color: #4E4E4E;
    text-decoration: underline 1px solid #C8C8C8;
}

@media screen and (max-width: 720px) {
    .programms__tab-link {
        font-size: 14px;
        line-height: 18px;
        margin-left: 20px;
    }
}